import React, { useEffect } from "react";
import { useQuery } from "@tanstack/react-query";
import { PaymentApi } from "../../services/payment.api";


export function VisitSlideComponent({ openLocalisationModal, openValidationModal, refetchHandler }) {
  const payment = PaymentApi();

  const fetchData = async () => {
    try {
      const res = await payment.findVisitsByUser();
      return res.data.data;
    } catch (err) {
      throw new Error("Erreur lors de la rcupration des utilisateurs : " + err.message);
    }
  };

  const { isLoading, data, error, refetch } = useQuery({ queryKey: ["allVisit"], queryFn: fetchData });

  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(() => { refetchHandler( refetch ) }, [refetch]);


  return (
    <div className="col-lg-12 border rounded-top-2">
      <div className="row d-flex align-item-center py-3 px-4 border-bottom">
        <span className="text-secondary lead"> Liste de mes rendez-vous </span>
      </div>

      <div className="row px-4 py-5">
        {isLoading ? (
          <div className="col-md-12 d-flex justify-content-center">
            <div className="spinner-border text-blue-clr fs-1" role="status" aria-label="Chargement"></div>
          </div>
        ) : (
          data?.map((item, index) => (
            <div className="col-md-6 mb-4" key={index}>
              <div className="d-flex flex-column shadow-sm bg-white rounded-3 border">
                <div className="d-flex p-3 border-bottom text-secondary">
                  <i className="bi bi-calendar me-2"></i> Rendez-vous de visite
                </div>
                <div className="p-3 border-bottom text-secondary">
                  Votre rendez-vous de visite pour la proprit situe {" "}
                  <span className="text-blue-clr">{item.Property.address}</span>, est programm le{" "}
                  <span className="text-blue-clr">{item.date}</span> {" "}
                  <span className="text-blue-clr">{item.time}</span>
                </div>
                <div className="d-flex justify-content-between bg-three-clr px-3 py-3">
                  <button className="btn btn-sm btn-outline-secondary" onClick={() => openLocalisationModal(item.Property.longitude, item.Property.latitude)} >
                    <i className="bi bi-geo-alt-fill me-1"></i> Localisation
                  </button>
                  { parseInt( item?.Property.isbooked ) === 1 ? (
                    <span className="text-secondary px-2 py-1 border rounded-2 small">
                      proprit prise
                    </span>
                  ) : (
                    <button className="btn btn-sm btn-outline-secondary" onClick={() => openValidationModal(item.Property.guaranty, item.Property.id) } >
                      Validation <i className="bi bi-arrow-right ms-1"></i>
                    </button>
                  )}
                </div>
              </div>
            </div>
          ))
        )}

        {error && (
          <div className="d-flex text-secondary">
            Une erreur est survenue, veuillez vrifier votre connexion
          </div>
        )}

        {Array.isArray(data) && data.length === 0 && (
          <div className="d-flex text-secondary">
            Aucune proprit n'a t rserve ou achete  votre compte.
          </div>
        )}
      </div>
    </div>
  );
}
