import React, { useEffect, useState }  from 'react';
import { useQuery } from "@tanstack/react-query";
import { FieldSkeleton, HouseSkeleton, LocationSkeleton, ServiceSkeleton, TestimonialSkeleton } from './Skeleton';
import { FieldContainer, HouseContainer, LocationContainer, TestimonialContainer } from './Container';
import Slider from 'react-slick';
import config from '../config/slickConf';
import { ServiceApi } from '../services/service.api';
import { ProductApi } from '../services/product.api';
import { TestimonialApi } from '../services/testimonial.api';

const service = ServiceApi();
const product = ProductApi();
const testimonial = TestimonialApi();


// ***************************************************************************************************************
export function ServiceComponent() 
{     
    const fetchServices = async () => {
        try {
            const res = await service.find(3);
            return res.data.data; 
        } catch (err) { 
            throw new Error('Erreur lors de la rcupration des utilisateurs : ' + err.message);
        }
    }
    const { isLoading, data, error } = useQuery({  queryKey: ["services"], queryFn: fetchServices });

    return (

        <div className="col-lg-10 mt-5">
          <div className="row justify-content-center">
            <div className="col-lg-6 col-md-8">
              <div className="d-flex flex-column align-items-center mb-3">
                <h3 className="text-secondary text-center mb-3" > Des services penss pour vous </h3>
                <p className="text-secondary text-center lead" > De la recherche de biens  la gestion locative, en passant par la vente et le conseil, nous proposons des services adapts  vos besoins pour vous aider  concrtiser vos ambitions immobilires. </p>
              </div>
            </div>
            <div className="col-lg-12">
              <div className="row py-5 d-flex justify-content-center"> 
              {  
                isLoading ? ( <div className="row"> <ServiceSkeleton value={ 3 } /> </div> ) : (
                data?.map( (item , index) => {
                    return (
                      <div className="col-lg-4 col-md-6 mb-4" key={index}>
                        <div className="shadow-sm rounded-2 d-flex flex-column align-items-center p-4 scale bg-white">
                          <span className="text-secondatry mb-3"> <i className="bi bi-patch-check fs-1"></i> </span>
                          <div className="card-body">
                            <p className="lead bold text-secondary text-center"> { item.title } </p>
                            <p className="lead text-secondary text-center"> { item.description } </p>
                          </div>
                        </div>
                      </div>
                    )
                }))
              }
              {
                error ? ( <div className="col-md-12 mt-3"> <div className="bg-white border rounded-2 p-5 text-secondary lead" > Une erreur est survenue lors du traitement. Veuillez verifier votre connexion </div> </div> ) : null
              }
              {
                ( Array.isArray(data) && data.length === 0 ) ? ( <div className="bg-white border rounded-2 p-5 text-secondary lead"> La liste de proprits, vide. </div> ) : null
              } 
              </div> 
            </div>
          </div>
        </div>
    )
}

// *******************************************************************************************************************
export function LocationComponent() 
{
    const settings = config();

    const fetchLocalities = async () => {
        try {
            const res = await product.findLocalities();
            return res.data.data; 
        } catch (err) { 
            throw new Error('Erreur lors de la rcupration des utilisateurs : ' + err.message);
        }
    }
    const { isLoading, data, error } = useQuery({  queryKey: ["localities"], queryFn: fetchLocalities });
        
    return (

        <div className="col-lg-10 my-5">
          <div className="row mb-3">
            <div className="col-lg-6 col-md-9">
              <div className="d-flex flex-column">
                <h2 className="text-secondary" > Des opportunits immobilires partout o vous etes </h2>
                <p className="text-secondary lead"> Nous oprons dans plusieurs rgions garantissant une couverture tendue et des biens adapts  toutes les prfrences : zones urbaines, rurales, littorales ou intermdiaires.</p>
              </div>
            </div>
            <div className="col-lg-12">
              <div className="row mt-4 d-flex justify-content-center">       
                { 
                    isLoading ? ( <div className="row"> <LocationSkeleton value={ 3 } design={"col-md-6 col-lg-4 mb-5"} /> </div> ) : (
                    <div className="slider-container">
                        <Slider {...settings}> 
                            { 
                                data?.map( (item , index) => { return (
                                    <LocationContainer region={ item.region } count={ item.count } index={index} />
                                )}) 
                            }
                        </Slider>
                    </div> ) 
                }  
                {
                    error ? ( <div className="col-md-12 mt-3"> <div className="bg-white border rounded-2 p-5 text-secondary lead my-3" > Une erreur est survenue lors du traitement. Veuillez verifier votre connexion </div> </div> ) : null
                }
                {
                    ( Array.isArray(data) && data.length === 0 ) ? ( <div className="bg-white border rounded-2 p-5 text-secondary lead my-3"> La liste de proprits, vide. </div> ) : null
                } 
              </div>
            </div>
          </div> 
        </div> 

    )
} 

// ****************************************************************************************************************
export function NearestHousesComponent() { 

    var settings = config()
    const [coords, setCoords] = useState(null);
    const [errCoord, setErrCoord] = useState(false);

    useEffect(() => {
      navigator.geolocation.getCurrentPosition(
        (item) => setCoords({ lat: item.coords.latitude, lon: item.coords.longitude }),
        (err) => setErrCoord(true)
      );
    }, []);

    const fetchNearest = async () => {
      const { lat, lon } = coords;
      const res = await product.findNearestHouse(lon, lat);
      return res.data.data;
    };

    const { isLoading, data, error } = useQuery({
      queryKey: ['nearestHouses', coords],
      queryFn: fetchNearest,
      enabled: !!coords,
    });

    return (

        <div className="col-lg-11 py-5"> 
            <div className="row mt-4 d-flex justify-content-center"> 
                { 
                    isLoading ? (  <div className="row"> <HouseSkeleton value={ 3 } design={"col-md-6 col-lg-4 mb-5"} />  </div>  ) : (
                    <div className="slider-container">
                        <Slider {...settings}> 
                            { 
                                data?.map( (item , index) => { return (
                                    <HouseContainer key={index} data={item} />
                                )}) 
                            }
                        </Slider>
                    </div> ) 
                } 
                {
                    errCoord ? ( <div className="bg-white border rounded-2 p-5 text-secondary lead"> Veuillez autoriser la golocalisation pour avoir la liste de nos logements qui vous sont proche. </div> ) : null
                } 
                {
                    error ? ( <div className="col-md-12 mt-3"> <div className="bg-white border rounded-2 p-5 text-secondary lead" > Une erreur est survenue lors du traitement. Veuillez verifier votre connexion </div> </div> ) : null
                }
                {
                    ( Array.isArray(data) && data.length === 0 ) ? ( <div className="bg-white border rounded-2 p-5 text-secondary lead"> La liste de proprits, vide. </div> ) : null
                }  
            </div> 
        </div> 
    )
}

