export function Navigation({ page, companyId })
{
    return (

        <div className="d-flex gap-4 align-items-center"> 
            <a className={`nav-link link-outline text-secondary pb-2 ${(page===1) ? 'link-active' : null}`} href={`/entreprise/${companyId}`}> Proprits-valides </a>
            <a className={`nav-link link-outline text-secondary pb-2 ${(page===2) ? 'link-active' : null}`} href={`/entreprise/${companyId}/propriete-en-cours`}> Proprits-en-cours </a>
            <a className={`nav-link link-outline text-secondary pb-2 ${(page===3) ? 'link-active' : null}`} href={`/entreprise/${companyId}/propriete-invalide`}> Proprits-invalides </a>
        </div>
    )
}